import React, { Component } from "react";
import {
  Button,
  Checkbox,
  Form,
  Icon,
  Step,
  Header,
  Placeholder,
  Message
} from "semantic-ui-react";

import "./checkout.css";
import { ShopContext } from "../../contexts/shop";


export default class CheckOut extends Component {
  state = { stage: 1, ok: true };
  validateStage1 = () => {
    
    return true;
    var state = this.state;
    if (!state.firstName) return false;
    if (!state.lastName) return false;
    if (!state.addressLine1) return false;
    if (!state.zip) return false;
    if (!state.city) return false;
    if (!state.accept) return false;

    return true;
  };
  handleChange = (e, { name, value }) => {
    
    var s = { [name]: value, stage1OK: this.validateStage1() }
    this.setState(s);
  };
  _setStage(stage) {
    this.setState({ stage });
  }
  render() {
    return (
      <ShopContext>
      {shop => (
      <div className="basketContainer">
        <Step.Group>
          {/* <Step
            active={this.state.stage === 0}
            onClick={() => {
              this._setStage(0);
            }}
          >
            <Icon name="address card" />
            <Step.Content>
              <Step.Title>Lidt om dig</Step.Title>
              <Step.Description>Dit navn og hvor du bor</Step.Description>
            </Step.Content>
          </Step> */}
          <Step
            active={this.state.stage === 1}
            onClick={() => {
              this._setStage(1);
            }}
          >
            <Icon name="truck" />
            <Step.Content>
              <Step.Title>Levering</Step.Title>
            </Step.Content>
          </Step>
          <Step
            active={this.state.stage === 2}
            onClick={() => {
              this._setStage(2);
            }}
          >
            <Icon name="payment" />
            <Step.Content>
              <Step.Title>Betaling</Step.Title>
            </Step.Content>
          </Step>

          <Step
            // disabled={true}
            active={this.state.stage === 3}
            onClick={() => {
              this._setStage(3);
            }}
          >
            <Icon name="confirm" />
            <Step.Content>
              <Step.Title>Bekræftelse</Step.Title>
            </Step.Content>
          </Step>
        </Step.Group>

        {this.state.stage === 1 && (
          <Form>
            <Form.Group widths="equal">
              <Form.Input
                onChange={this.handleChange}
                label="Fornavn"
                name="firstName"
                placeholder="Indtast fornavn"
              />

              <Form.Input
                label="Efternavn"
                name="lastName"
                placeholder="Indtast efternavn"
                onChange={this.handleChange}
              />
            </Form.Group>
            <Form.Group widths="equal">
              <Form.Input
                label="Adresse"
                name="addressLine1"
                placeholder="Indtast vej og husnummer + evt. etage"
                onChange={this.handleChange}
              />
            </Form.Group>
            <Form.Group widths="equal">
              <Form.Input
                name="zip"
                label="Postnummer"
                placeholder="Indtast postnummer"
              />

              <Form.Input
                label="By"
                name="city"
                placeholder="By"
                onChange={this.handleChange}
              />
            </Form.Group>
            <Form.Group widths="equal">
            <Form.Input
                onChange={this.handleChange}
                label="Email"
                name="email"
                placeholder="Indtast din email"
              />
              <Form.Input
                name="phoneNumber"
                label="Telefonnummer"
                placeholder="Indtast telefonnummer"
                onChange={this.handleChange}
              />
            </Form.Group>
            <Form.TextArea
              name="comments"
              onChange={this.handleChange}
              label="Hvis du vil have det leveret et andet sted (f.eks. i skolen), så skriv det her i feltet eller SMS til 31 69 66 75 dit navn og ønsket leveringssted"
            />

            <div>
              McCool.dk er ejet Emil Gregers Hedegaard Johansen, Lysskovvej 2,
              3060 Espergærde . De varer som du bestiller her bliver lavet efter
              bestilling individuelt til dig, så du kan ikke få pengene tilbage
              når du har modtaget varerne
            </div>
            <Form.Checkbox
              name="accept"
              label="Jeg accepterer betingelserne"
              onChange={this.handleChange}
            />
            <Button
              disabled={!this.state.stage1OK}
              onClick={() => {
                this.setState({ stage: 2 });
              }}
              type="submit"
            >
              Til betaling
            </Button>
          </Form>
        )}

        {this.state.stage === 2 && (
          <div>
            <img
              style={{ height: "32px", width: "auto" }}
              src="https://i2.wp.com/www.californiawine.dk/wp-content/uploads/2018/09/mobile-pay-logo-2.png"
            />
            <p>MobilePay {shop.price()},00 til 31 69 66 75!</p>
            <p>
              <small>
                (Jeg er ikke klar til at tage imod kreditkort endnu)
              </small>
            </p>

            <Form>
              <Form.Checkbox
                name="payed"
                label="Jeg har betalt"
                onChange={this.handleChange}
              />
              <ShopContext>{shop=>
              <Button
                xdisabled={!this.state.error}
                onClick={() => {
                  shop.send(this.state,shop)
                  .then(()=>{
                    this.setState({ stage: 3 });
                  })
                  .catch(error=>{
                    this.setState({error:error.message})
                  })
                 
                }}
              >
                Send bestilling
              </Button>}
              
              </ShopContext>
            </Form>
            {this.state.error &&
            <Message
            error
            header='Der opstod en fejl'
            list={[
              this.state.error              
            ]}
          />
              }
          </div>
        )}
        {this.state.stage === 3 && (
          <div>
            Din bestilling er modtaget
            {/* <Placeholder>
              <Placeholder.Header>
                <Placeholder.Line length="very short" />
                <Placeholder.Line length="medium" />
              </Placeholder.Header>
              <Placeholder.Paragraph>
                <Placeholder.Line length="short" />
              </Placeholder.Paragraph>
            </Placeholder> */}
          </div>
        )}
      </div>
       )}
       </ShopContext>
    );
  }
}
