import React, { Component } from "react";
import { Table,Button, Icon } from "semantic-ui-react";
import { ShopContext } from "../../contexts/shop";
import "./basket.css";
import { navigate } from "@reach/router";
export default class Basket extends Component {
  render() {
    return (
      <div className="basketContainer">
        <ShopContext>
          {shop => (
            <Table striped>
              <Table.Header>
                <Table.Row>
                  
                  <Table.HeaderCell>Produkt</Table.HeaderCell>
                  <Table.HeaderCell></Table.HeaderCell>
                  <Table.HeaderCell  textAlign='right'>Pris</Table.HeaderCell>
                  <Table.HeaderCell>&nbsp;</Table.HeaderCell>
                </Table.Row>
              </Table.Header>

              <Table.Body>
                {shop.items.map((item, index) => {
                  return (
                    <Table.Row key={index}>
                      
                      <Table.Cell>{item.displayName} - {item.size}</Table.Cell>
                      <Table.Cell></Table.Cell>
                      <Table.Cell textAlign='right'>{item.price}</Table.Cell>
                      <Table.Cell><Icon style={{cursor:"pointer"}} onClick={()=>{shop.removeItem(index)}} name="delete" /> </Table.Cell>
                    </Table.Row>
                  );
                })}
                <Table.Row >
                     
                      <Table.Cell>&nbsp;</Table.Cell>
                      <Table.Cell>Fragt</Table.Cell>
                      <Table.Cell textAlign='right'>{shop.freight}</Table.Cell>
                    </Table.Row>
               <Table.Row >
                     
                      <Table.Cell>&nbsp;</Table.Cell>
                      <Table.Cell>Samlet pris</Table.Cell>
                      <Table.Cell textAlign='right'>{shop.totalPrice}</Table.Cell>
                    </Table.Row>
              </Table.Body>
            </Table>
          )}
        </ShopContext>

        <Button primary  onClick={()=>{navigate("/checkout")}}>
            Gå til "kassen"
           </Button>
           <Button secondary  onClick={()=>{navigate("/")}}>
           Shop videre
           </Button>
      </div>
    );
  }
}
