import React, { Component } from "react";
import { Dropdown, Grid, Image, Header } from "semantic-ui-react";
import { Button } from "semantic-ui-react";
import "./product.css";

import { ShopContext } from "../../contexts/shop";

var GetSKU = require("../../services/inventory").get;
export default class ProductPage extends Component {
  state = { item: {} };
  handleChange = (e, { name, value }) => this.setState({ [name]: value });

  componentDidMount() {
    GetSKU(this.props.sku).then(item => {
      var sizeOptions = item.sizes.split(",").map(s => {
        return {
          text: s,
          value: s
        };
      });
      this.setState({ item, image: item.image, sizeOptions });
    });
  }
  render() {
    var item = this.state.item;
    return (
      <Grid>
        <Grid.Column width={8}>
          <Image src={this.state.image} />
          <Header as="h1"> {item.displayName}</Header>
          <div className="productTitle product" />
          {/* <div className="productDescription product">{item.description}</div> */}
        </Grid.Column>
        <Grid.Column width={4}>
          <div
            style={{ cursor: "pointer" }}
            onClick={() => {
              this.setState({ image: item.image });
            }}
          >
            <Image
             
              src={item.image}
            />
          </div>
          <div
            style={{ cursor: "pointer" }}
            onClick={() => {
              this.setState({ image: item.image2 });
            }}
          >
            <Image
          
              src={item.image2}
            />
          </div>
          <Header as="h2">
          Kr. {item.price},00</Header>
        
            <Dropdown
              name="size"
              placeholder="Vælg størrelse"
              fluid
              selection
              options={this.state.sizeOptions}
              onChange={this.handleChange}
            />
         
          <div className="callForAction">
            <ShopContext>
              {shop => (
                <Button
                  disabled={!this.state.size}
                  primary
                  onClick={() => {
                    shop.addItem(this.state.item, this.state.size);
                  }}
                >
                  Læg i kurven
                </Button>
              )}
            </ShopContext>
          </div>
        </Grid.Column>
      </Grid>
    );
  }
}
