import React, { Component } from 'react'
import Product from '../../components/Product';
import "./gallery.css"
var INVENTORY = require("../../services/inventory")

export default class Gallery extends Component {
    state = {}
    componentDidMount (){
        INVENTORY.inventory().then(items=>(
            this.setState({items})
        ))
    }
    render() {
        var items = this.state.items ? this.state.items : []
        return (
            <div className="gallery-container">
            {items.map((item,ix)=>{
                
                return  <div className="gallery-item"> <Product {...item} key={ix}/></div>
            })
          
        }
        </div>
        )
    }
}
