import React, { Component } from "react";
import "./App.css";
import { Router, Match, Location, navigate } from "@reach/router";
import HomePage from "./pages/HomePage";
import Header from "./components/Header";
import Footer from "./components/Footer";
import ProductPage from "./pages/ProductPage";
import Basket from "./containers/Basket";
import { ShopContext } from "./contexts/shop";
import CheckOut from "./containers/CheckOut";
import { SIGUNUSED } from "constants";
var host = "https://api.jumpto365.com"
const FREIGHT = 40
class App extends Component {
  calcSum = (items) => {
    var sum = 0;
    items.forEach(item => {
      sum += item.price;
    });

    return sum+FREIGHT;
  };

  addItem = (item, size) => {
    var items = this.state.items;
    items.push({ ...item, size });
    localStorage.setItem("basket", JSON.stringify(items));
    this.setState({ items, totalPrice: this.calcSum(items) });
  };

  removeItem = index => {
    var items = this.state.items;
    items.splice(index, 1);
    localStorage.setItem("basket", JSON.stringify(items));
    this.setState({ items, totalPrice: this.calcSum(items) });
  };
  price = () =>{
    return this.calcSum(this.state.items)
  }
  send = (contactData, store) => {
    return new Promise((resolve, reject) => {
      console.log(contactData);
      //return reject({message:"asfdsafdsf"})
      var url = `${host}/v1.0/route`
      fetch(
        url,
        {
          method: "POST", // *GET, POST, PUT, DELETE, etc.
          mode: "cors", // no-cors, cors, *same-origin
          cache: "no-cache", // *default, no-cache, reload, force-cache, only-if-cached
          credentials: "same-origin", // include, *same-origin, omit
          headers: {
            "Content-Type": "application/json"
            // "Content-Type": "application/x-www-form-urlencoded",
          },
          redirect: "follow", // manual, *follow, error
          referrer: "no-referrer", // no-referrer, *client
          body: JSON.stringify({ type:"mccool.order",contactData, store }) // body data type must match "Content-Type" header
        }
      ).then(response => {
        //response.json()
        var items = [];
        localStorage.setItem("basket", JSON.stringify(items));
        this.setState({ items, totalPrice: this.calcSum(items) });
        resolve();
      });
    });
  };
  constructor(props) {
    super(props);
    this.state = {
      items: [],
      totalPrice: 0,
      freight: FREIGHT,
      addItem: this.addItem,
      removeItem: this.removeItem,
      price : this.price,
      send: this.send
    };
  }

  componentDidMount() {
    var basket = localStorage.getItem("basket");
    try {
      var items = JSON.parse(basket);
      if (items.length) {
        this.setState({ items, totalPrice: this.calcSum(items) });
      }
    } catch (error) {}
  }
  render() {
    return (
      <div>
        <ShopContext.Provider value={this.state}>
          <ShopContext.Consumer>
            {shop => (
              <Header totalPrice={shop.totalPrice} items={shop.items.length} />
            )}
          </ShopContext.Consumer>

          <Router>
            <Basket path="/basket" />
            <CheckOut path="/checkout" />
            <HomePage path="/" />
            <ProductPage path="/product/:sku" />
          </Router>
          <Footer />
        </ShopContext.Provider>
      </div>
    );
  }
}

export default App;
