import React, { Component } from "react";
import { Spring } from "react-spring/renderprops";

import "./product.css";
import { navigate } from "@reach/router";
import { throws } from "assert";
export default class Product extends Component {
  state = {};
  timer = null;
  mouseOut = () => {
    var that = this;
    this.timer = setTimeout(() => {
      that.setState({ mouseOver: false });
      that.timer = null;
    }, 100);
  };

  mouseOver = () => {
    if (this.timer) {
      clearTimeout(this.timer);
      this.timer = null;
    }
    this.setState({ mouseOver: true });
  };
  render() {
    return (
      <div
        className="ProductMiniature"
        onMouseOver={this.mouseOver}
        onMouseOut={this.mouseOut}
        onClick={() => {
          navigate(`/product/${this.props.sku}`);
        }}
      >
        <div style={{ position: "relative" }}>
          <div style={{ position: "absolute" }}>
            <img className="Product" src={this.props.image} />
          </div>
          {this.state.mouseOver && (
            <Spring
              from={{ height: 0, opacity: 0.0 }}
              to={{ height: 200, opacity: 0.7 }}
            >
              {styles => (
                <div
                  onMouseOver={this.mouseOver}
                  onClick={() => {
                    navigate(`/product/${this.props.sku}`);
                  }}
                  style={{
                    padding: "16px",
                    position: "absolute",
                    width: "200px",
                    color: "#ffffff",
                    backgroundColor: "#B30CE8",
                    height: styles.height,
                    opacity: styles.opacity
                  }}
                >
                  <h1>{this.props.displayName}</h1>
                  <ul>
                    <li>Størrelse: {this.props.sizes} </li>
                    <li>Pris: Kr. {this.props.price},00 </li>
                  </ul>
                </div>
              )}
            </Spring>
          )}
        </div>
      </div>
    );
  }
}
