var index = {};
var data = priceList.map(price => {
  var item = {
    sku: price.SKU,
    design: "",
    displayName: price["Display Name"],
    image: price.ImageFront,
    image2: price.ImageBack,
    price: price.Price,
    sizes: price.Sizes,
    description: price.Description
  };
  index[price.SKU] = item;
  return item;
});
